<template>
  <div id="app">

<transition name="c-fade" mode="out-in">
<div class="relative" style="background: url('') center center no-repeat;
 background-size: contain; padding-bottom: calc(100%*800/1600); overflow: hidden;">
  <transition
    v-on:before-enter="beforeEnter"
    v-on:enter="enter"
    v-on:leave="leave"
    v-bind:css="false"
  >
  <div v-if="this.slidestate === 0" key="s0" class="absolute">
  <img src="./assets/sd_slide_i1.jpg" style="width:100vw" />
  </div>
  <div v-if="this.slidestate === 1" key="s1" class="absolute">
  <img src="./assets/sdcar4.jpg" style="width:100vw" />
  </div>
  <div v-if="this.slidestate === 2" key="s2" class="absolute">
  <img src="./assets/sdcar6.png" style="width:100vw" />
  </div>
  <div v-if="this.slidestate === 3" key="s3" class="absolute">
  <img src="./assets/sd_slide_i3.jpg" style="width:100vw" />
  </div>
  <div v-if="this.slidestate === 4" key="s4" class="absolute">
  <img src="./assets/sd_slide_i4.jpg" style="width:100vw" />
  </div>
  <div v-if="this.slidestate === 5" key="s5" class="absolute">
  <img src="./assets/sd_slide_i4.jpg" style="width:100vw" />
  </div>
  </transition>
</div>
</transition>

  </div>
</template>

<script>
  import log from 'loglevel'
  import velocity from 'velocity-animate/velocity'
  export default {
    components: {
      log,
      velocity
    },
    name: 'app',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        slidestate: -1,
        innerH: window.innerHeight,
        innerW: window.innerWidth
      }
    },
    created: function () {
      log.setLevel(0)
      log.debug('// created')
    },
    mounted: function () {
      log.debug('// mounted')
      this.init()
    },
    computed: {
      showSlide: function () {
        switch (this.slidestate) {
          case 1: return 1
        }
      }
    },
    methods: {
      init: function () {
        log.debug('// init')
        log.debug(window.innerWidth)
        log.debug(window.innerHeight)
        log.debug(velocity)
        this.show = true
        this.runSlide()
      },
      setSlidestate: function (num) {
        log.debug('// setSlidestate')
        this.slidestate = num
      },
      runSlide: function () {
        log.debug('// showLoop')
        setTimeout(function () { this.setSlidestate(0) }.bind(this), 500)
        setTimeout(function () { this.setSlidestate(1) }.bind(this), 3000)
        setTimeout(function () { this.setSlidestate(2) }.bind(this), 5000)
        setTimeout(function () { this.setSlidestate(3) }.bind(this), 8000)
        setTimeout(function () { this.setSlidestate(4) }.bind(this), 11000)
      },
      beforeEnter: function (el) {
        log.debug('// beforeEnter')
        log.debug(el)
        el.style.opacity = 0
      },
      enter: function (el, done) {
        log.debug('// enter')
        switch (this.slidestate) {
          case 0:
            log.debug(this.slidestate)
            velocity(el, { opacity: [ 1, 'easeInSine', 0 ] }, { duration: 1000 })
            break
          case 1:
            log.debug(this.slidestate)
            velocity(el, { opacity: [ 1, 'easeInSine', 0 ] }, { duration: 2000 })
            break
          case 2:
            log.debug(this.slidestate)
            velocity(el, { translateX: [ window.innerWidth, window.innerWidth * -0.8 ], opacity: [ 0, 'easeInOutSine', 1 ] }, { duration: 7000, loop: 0 })
            break
          case 3:
            log.debug(this.slidestate)
            velocity(el, { opacity: [ 1, 'easeInSine', 0 ] }, { duration: 2000 })
            break
          case 4:
            log.debug(this.slidestate)
            velocity(el, { opacity: [ 1, 'easeInSine', 0 ] }, { duration: 2000 })
            break
          default:
            velocity(el, { translateX: [ window.innerWidth, 0 ], opacity: [ 0, 'easeInSine', 1 ] }, { duration: 2000, loop: 1 })
        }
        // velocity(el, { opacity: 1, fontSize: '2.4em' }, { duration: 300 })
        // velocity(el, { fontSize: '3.0em' }, { complete: done })
      },
      afterEnter: function (el) {
      },
      enterCancelled: function (el) {
      },
      // --------
      // LEAVING
      // --------
      beforeLeave: function (el) {
      },
      leave: function (el, done) {
        log.debug('// leave')
/*
        velocity(el, { translateX: '15px', rotateZ: '50deg' }, { duration: 600 })
        velocity(el, { rotateZ: '100deg' }, { loop: 2 })
        velocity(el, {
          rotateZ: '45deg',
          translateY: '30px',
          translateX: '30px',
          opacity: 0
        }, { complete: done })
*/
      },
      afterLeave: function (el) {
      },
      leaveCancelled: function (el) {
      }
    }
  }
</script>

